import React from 'react'
import './index.less'
import {Avatar, Button, Modal, Spin} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import {UploadWorkDone} from "./UploadWorkDone";
import {Link, Redirect, Route} from "react-router-dom";
import MyFav from "../MyFav";
import Expired from "./Expired";

export default class TaskAcepted extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            contentModal: '',
            visibleCommunications: false,
            worksIng: [],
            worksExpired: [],
        }
    }

    componentWillMount() {

        console.log(this.state);
        this.getFav()
    }

    getCommunications = (id) => {
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            // console.log(p);
            if (p.id === id) {
                return (
                    <div>
                        <h2 className='phone'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5>提示：长按识别二维码添加微信</h5>
                    </div>
                )
            }
        }
    };
    getFav = () => {
        // 有无缓存？返回：请求->缓存->返回
        const cached = localStorage.my_works !== undefined;
        if (cached) {
            // 返回数据
            this.setState({
                myFavs: JSON.parse(localStorage.my_works),
            })
        } else {
            // 请求
            axios.get(
                baseUrl + '/api/get_my_works',
                {
                    params: {
                        token: localStorage.customer_token,
                        type: 'all',
                    }
                })
                .then((Response) => {
                    console.log('请求了my_works');

                    const ws = Response.data.works;
                    let worksIng = [];
                    let worksExpired = [];
                    for (const w in  ws) {
                        const p = ws[w];
                        if (moment().unix() > p.stop_at) {
                            // 过期
                            worksExpired.push(p);
                        } else {
                            worksIng.push(p);
                        }
                    }
                    console.log('e', worksExpired);
                    console.log('i', worksIng);

                    this.setState({
                        myFavs: Response.data.works,
                        worksExpired: worksExpired,
                        worksIng: worksIng,
                    });
                    // 设置缓存
                    localStorage.my_works = JSON.stringify(Response.data.works)
                });
        }

    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            contentModal: this.getCommunications(id),
        });
    };
    uploadWork = (id) => {
        console.log(id);
        localStorage.id_work = id;
        this.setState({
            visibleCommunications: true,
            contentModal: <UploadWorkDone/>,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    renderFav = () => {
        if (this.state.myFavs.length === 0) {
            return <h2>暂无</h2>
        }
        let fs = [];
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            fs.push(
                <div
                    className=
                        {
                            p.stop_at < moment().unix()
                                ? 'cell-work-index cell-disable'
                                : 'cell-work-index'
                        }
                    id={p.id}>
                    <div className='work-cell-row'>
                        {/*标题*/}
                        <span className='work-name'>{p.work_name}</span>
                        {/*报酬*/}
                        <span
                            className='work_money'>报酬:{p.work_money !== 0 ? p.work_money : '可以协商'}{p.work_tpl === 2 || p.work_tpl === 3 ? '金币' : '¥'}</span>
                    </div>
                    <div className='work-cell-row'>
                        <span> </span>
                        <span>需要:{p.work_person_number}人</span>
                    </div>

                    <div className='work-cell-row'>
                        {/*分类*/}
                        <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                        {p.stop_at ?
                            <span>截止时间:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    '今天内'
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }

                                </span>
                            : null}
                    </div>
                    <div className='work-cell-row'>
                        {/*头像*/}
                        <span>

                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'>头像</Avatar>
                        {/*名字*/}
                        <span className='name'>{p.senter_name}</span>
                        </span>
                        {/*报名按钮*/}
                        {
                            //过期判定
                            p.stop_at < moment().unix()
                                ? <Button disabled>已过期</Button>
                                : <div>
                                    <Button
                                        className='button-margin'
                                        type='primary'
                                        onClick={this.showCommunications.bind(this, p.id)}>联系</Button>
                                    <Button
                                        className='button-margin'
                                        type='primary'
                                        onClick={this.uploadWork.bind(this, p.id)}>提交审核</Button>
                                </div>
                        }
                        {/*查看详情*/}
                        <span className='button-more'>
                            <Link to={`/w/${p.id}`}>
                                <Button
                                >查看详情</Button>

                                </Link>
                            </span>
                    </div>
                </div>
            )
        }

        return fs
    };

    render() {
        return (
            this.state.myFavs !== undefined
                ?
                <div className='my-favs'>
                    {/* 子路由导航 */}
                    {/*<div className='nav-route'>*/}

                    {/*    <Link*/}
                    {/*        to={`${this.props.match.url}/Ing`}*/}
                    {/*        className={document.location.hash.includes('Ing') ? 'active' : 'unactive'}*/}
                    {/*    >*/}
                    {/*        正在进行*/}
                    {/*    </Link>*/}
                    {/*    <Link*/}
                    {/*        to={`${this.props.match.url}/Expired`}*/}
                    {/*        className={document.location.hash.includes('Expired') ? 'active' : 'unactive'}*/}
                    {/*    >*/}
                    {/*        已过期*/}
                    {/*    </Link>*/}
                    {/*</div>*/}
                    {/*<Route exact path={this.props.match.url} render={() => (*/}
                    {/*    <Redirect to={`${this.props.match.url}/Ing`}/>*/}
                    {/*)}/>*/}
                    {/*<Route exact path={`${this.props.match.url}/Ing`} component={Expired}/>*/}
                    {/*<Route exact path={`${this.props.match.url}/Expired`} component={Expired}/>*/}
                    {this.renderFav()}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.state.contentModal}
                    </Modal>
                </div>
                : <Spin/>

        )

    }
}
